/* Products.css */

.products {
    background-color: #f8f8f8;
    padding-top: 80px;
  }
  
  .section__title {
    text-align: left;
    font-size: 1rem;
    margin-bottom: 30px;
  }

  .section__title2 {
    text-align: left;
    font-size: 1rem;
    margin-bottom: 30px;
    color:#C71102;
  }

  .products__container {
    display: flex;
    justify-content: space-between;
    max-width: 1400px;
    margin: 0 auto;
    padding: 10px;
    padding-top: 20px;
    gap: 20px;
  }
  
  .leftcont {
    flex: 0 0 20%;
    margin-top: 40px;
  }
  
  .rightcont {
    flex: 0 0 80%;
    margin-top: 40px;
  }
  
  .products__filters {
    list-style: none;
    display: table;
    font-size: small;
    justify-content: center;
    margin-bottom: 30px;
  }

  
  
  .products__item {
    cursor: pointer;
    padding: 10px 20px;
    margin: 0 10px;
    border: 1px solid #ddd;
    border-radius: 5px;
    transition: background-color 0.3s;
    text-align: center;
    display: flex;
    justify-content: center;



  }
  
  .products__item:hover {
    background-color: rgba(128, 128, 128, 0.512);
  }
  
  .active-product {
    background: linear-gradient(to right, #ff0000, #d41919, #8b0000);
    color: #fff;
  }
  
  .products__title {
    font-size: 0.7rem;

  }
  
  .products__stock {
    font-size: 0.7rem;
    color: #040404;
    font-weight: 600;
    margin-left: 20px;
  }

  
  
  .products__content {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    gap: 30px;
  }
  
  .products__card {
    position: relative;
    overflow: hidden;
    border: 1px solid #ddd;
    border-radius: 10px;
    transition: transform 0.3s, opacity 0.3s;
  }
  
  .fade-in {
    opacity: 1;
    transform: translateY(0);
  }
  
  .fade-in.active {
    transform: translateY(-10px);
  }
  
  .products__shape {
    overflow: hidden;
    border-radius: 10px 10px 0 0;
  }
  
  .products__img {
    width: 100%;
    height: auto;
    display: block;
  }
  
  .products__data {
    padding: 20px;
  }
  
  .products__name1 {
    font-size: 1.2rem;
    /* margin-bottom: 5px; */
  }
  
  .products__availability{
        color: #4caf50;
        font-size: 0.9rem;
      
      }
  
  .products__size2,
  .products__details,
  .products__details2,
  .info-message {
    font-size: 0.7rem;
    color: #555;
    margin: -5px;
  }

  .products__img {
    width: 100%;
    height: 200px; /* Set a fixed height for all images, adjust as needed */
    object-fit: cover; /* Ensure the image covers the specified height */
    display: block;
  }
  
  .products__button2,
  .button {
    display: block;
    width: 100%;
    padding: 5px;
    margin-top: 10px;
    background-color: rgb(255, 160, 17);
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .products__button2:hover,
  .button:hover {
    background-color: #08440b;
  }
  
  .info-message {
    background-color: #f44336;
    color: #fff;
    padding: 10px;
    border-radius: 5px;
    margin-top: 10px;
    display: inline-block;
  }





  /* Media Queries */

@media only screen and (max-width: 768px) {
  .products__container {
      flex-direction: column;
      align-items: center;
  }

  .leftcont,
  .rightcont {
      flex: 0 0 100%;
  }

  .products__item {
      margin: 0;
  }

  .products__filters {
      text-align: center;
  }

  .products__content {
      grid-template-columns: 1fr;
  }
}